import React, { useEffect, useState, useRef } from 'react';
import SenateTracker from '../components/SenateTracker';
import HouseTracker from '../components/HouseTracker';
import Spinner from '../components/Spinner';
import ResultsRecordList from '../components/ResultsRecordList';
import MyMap from '../components/MyMap';
import TwitterWidget from '../components/TwitterWidget';
import '../css/index.css';

const Home = () => {
    const [records, setRecords] = useState([]);
    const [raceRecords, setRaceRecords] = useState([]);
    const [resultsRecords, setResultsRecords] = useState([]);
    const [senateCount, setSenateCount] = useState([36, 29]);
    const [govCount, setGovCount] = useState([6, 8]);
    const [houseCount, setHouseCount] = useState([0, 0]);
    const [mode, setMode] = useState('SENATE');
    const [resultsYear, setResultsYear] = useState(2022);

    const [updateHouseWidget, setUpdateHouseWidget] = useState(0);
    const [isLoading, setIsLoading] = useState(false);

    const page = 'LIVE';

    const governorResultsYear = 2018, senateResultsYear = 2020, houseResultsYear = 2020, currentYear = 2022;

    useEffect(() => {
        async function getRecords() {
            if (isLoading === false) {setIsLoading(true)}
            const candidatesResponse = await fetch(`http://localhost:3001/candidatesRecord/`);

            if (!candidatesResponse.ok) {
                const message = `An error occurred: ${candidatesResponse.statusText}`;
                window.alert(message);
                return;
            } else {
                setIsLoading(false);
            }

            const candidatesList = await candidatesResponse.json(), records = [];

            for (let x of candidatesList) {
                if (x.type === 'SENATE') {
                    records.push(x)
                }
            }
            
            setRecords(rankSort(records));
            //if (isLoading === true) {setIsLoading(false)}

        }

        async function getResults() {
            if (isLoading === false) {setIsLoading(true)}
            const resultsResponse = await fetch(`http://localhost:3001/resultsRecord/`);

            if (!resultsResponse.ok) {
                const message = `An error occurred: ${resultsResponse.statusText}`;
                window.alert(message);
                return;
            } else {
                setIsLoading(false);
            }

            const resultsList = await resultsResponse.json(), resultsRecords = [];

            for (let x of resultsList) {
                if ((x.type === 'GOVERNOR' && (x.year === governorResultsYear || x.year === currentYear)) || (x.type === 'SENATE' && (x.year === senateResultsYear || x.year === currentYear)) || (x.type === 'HOUSE' && (x.year === houseResultsYear || x.year === currentYear))) {
                    resultsRecords.push(x);
                }
            }
            
            setResultsRecords(resultsRecords);

        }

        async function getRaces() {
            if (isLoading === false) {setIsLoading(true)}
            const racesResponse = await fetch(`http://localhost:3001/racesRecord/`);

            if (!racesResponse.ok) {
                const message = `An error occurred: ${racesResponse.statusText}`;
                window.alert(message);
                return;
            } else {
                setIsLoading(false);
            }

            const racesList = await racesResponse.json(), raceRecords = [];

            for (let x of racesList) {
                if (x.year === resultsYear) {
                    raceRecords.push(x);
                }
            }
            
            setRaceRecords(raceRecords);

        }

        function getCounts() {
            let houseDem = 0, houseRep = 0, senateDem = 0, senateRep = 0, govDem = 0, govRep = 0;

            for (let record of resultsRecords) {
                if (record.called !== '' && record.year === resultsYear) {
                    if (record.type === 'SENATE') {
                        if (record.called === 'Democratic') {
                            senateDem += 1;
                        } else if (record.called === 'Republican') {
                            senateRep += 1;
                        }
                    } else if (record.type === 'HOUSE') {
                        if (record.called === 'Democratic') {
                            houseDem += 1;
                        } else if (record.called === 'Republican') {
                            houseRep += 1;
                        }
                    } else if (record.type === 'GOVERNOR') {
                        if (record.called === 'Democratic') {
                            govDem += 1;
                        } else if (record.called === 'Republican') {
                            govRep += 1;
                        }
                    }
                }
            }

            setHouseCount([houseDem, houseRep]);
            setSenateCount([senateDem + 36, senateRep + 29]);
            setGovCount([govDem + 6, govRep + 8]);
        }

        getRecords();
        getResults();
        getRaces();
        getCounts();

        return;

    }, [mode]);

    function rankSort(array) {
        const length = array.length;
      
        for (let i = 0; i < length; i++) {
          for (let j = 0; j < length; j++) {
            if (array[i].ratingRank > array[j].ratingRank) {
              const temp = array[i];
              array[i] = array[j];
              array[j] = temp;
            } else if (array[i].ratingRank === array[j].ratingRank) {
              if (array[i].state < array[j].state) {
                const temp = array[i];
                array[i] = array[j];
                array[j] = temp;
              }
            }
          }
        }
      
        return array;
    }

    return (
        <div className="mainPage">
            <h1>THE 2022 US MIDTERM ELECTIONS</h1>
            <p>&emsp;Welcome to Noah's Election Machine! Here you can view live election results for every U.S. Senate, House of Representatives, and Governor
                election for the 2022 midterms. Check back on November 8th to view results for every election all in one place!<br /><br />&emsp;You can
                also check out our Call Simulator to see how each race impacts control of the Senate, House, and Governorships, or you can
                visit our Past Results page to view previous election results for every state. 
            </p>
            <div className='typeInfo'>
                <div className="senateTitleBG">
                    <div className="senateTitle">
                        <h2>{mode} ELECTION TRACKER</h2>
                    </div>
                </div>
                {mode === 'SENATE' &&
                    <p>
                        &emsp; The U.S. senate elections are shaping up to be an interesting deviation from the typical
                        midterm year. Since Democrats control both chambers of Congress along with the White House, Republicans would
                        normally be expected to reclaim the senate. Oddly enough, so far, it looks like the Democrats are expected
                        to retain control. This is largely due to the selection of weak GOP candidates across several key swing states, hurting
                        the party's chances of taking back the chamber.<br /><br />&emsp;As it stands, the GOP's keys to the senate will be through Nevada and Georgia,
                        assuming the Democrats are able to pick up a seat in Pennsylvania, which they are favored to do at time of writing.
                    </p>
                } {mode === 'GOVERNOR' &&
                    <p>
                        &emsp; The race for control of the most governorships looks to be following a similar path to the senate elections.
                        Candidate quality is what seems to matter most here, moreso than the national environment.<br /><br />&emsp;Republicans are favored to hold the
                        most governorships, as is normally the case, but if things go well for the Democrats, we could possibly see a 
                        25-25 split between the two parties. This will ultimately come down to the most competitive races in Wisconsin,
                        Oregon, Arizona, and Nevada. 
                    </p>
                } {mode === 'HOUSE' &&
                    <p>
                        &emsp; The 2022 U.S. House of Representatives elections are much more in line with what we would expect for a midterm year
                        when compared to the senate and governorships. Republicans are favored to take back the house this November, primarily due to 
                        the national environment, along with recent redistricting wins in key states.<br /><br />&emsp;Still, this is not set in stone, and
                        there are plenty of tight races that Democrats have a shot at winning in states like New York, Arizona, and California.
                    </p>
                }
            </div>
            <div className='modeButtons'>
                <button onClick={() => {setMode('SENATE');}}>SENATE</button>
                <button onClick={() => {setMode('HOUSE')}}>HOUSE</button>
                <button onClick={() => {setMode('GOVERNOR')}}>GOVERNORS</button>
            </div>
            {mode === 'HOUSE' &&
                <HouseTracker page={page} resultsYear={resultsYear} updateHouseWidget={updateHouseWidget} setUpdateHouseWidget={setUpdateHouseWidget} resultsRecords={resultsRecords} raceRecords={raceRecords} houseCount={houseCount} setHouseCount={setHouseCount}/>
            }
            {mode !== 'HOUSE' &&
                <MyMap page={page} isLoading={isLoading} resultsYear={resultsYear} raceRecords={raceRecords} resultsRecords={resultsRecords} setSenateCount={setSenateCount} setGovCount={setGovCount} mode={mode} setMode={setMode}/>
            }
            {mode !== 'HOUSE' &&
            <hr />
            }
            {//<SenateTracker records={records} className={'senateTrackerFull'}/>}
}
            <ResultsRecordList page={page} year={resultsYear} records={resultsRecords} setRecords={setResultsRecords} raceRecords={raceRecords} type={mode}/>
        </div>
    );
}

export default Home;